
@charset "utf-8";
//  Base -> Typography
// --------------------------------------------------
h1 {
  // font-size: $h1-font-size;
  font-size: clamp($h2-font-size, 4.5vw, $h1-font-size);
}

h2 {
  // font-size: $h2-font-size;
  font-size: clamp($h3-font-size, 4.0vw, $h2-font-size);
}

h3 {
  // font-size: $h3-font-size;
  font-size: clamp($h4-font-size, 3.5vw, $h3-font-size);
}

h4 {
  // font-size: $h4-font-size;
  font-size: clamp($h5-font-size, 3.0vw, $h4-font-size);
}

h5 {
  // font-size: $h5-font-size;
  font-size: clamp($h6-font-size, 2.5vw, $h5-font-size);
}

h6 {
  // font-size: $h6-font-size;
  font-size: clamp($para-font-size, 2.0vw , $h6-font-size);
}

p {
  // font-size: $para-font-size;
  font-size: clamp($span-font-size, 1.25vw , $para-font-size);
}

span {
  font-size: $span-font-size;
}

label {
  font-size: $label-font-size;
}

b {
  font-weight: 500;
}

srtong {
  font-weight: 700;
}

em {
  font-style: italic;
}

small {
  font-size: 75%;
}

big {
  font-size: 125%;
}

abbr {
    border-bottom: .125rem dotted;
    text-decoration: none;
    cursor: help;
}
mark {
    border-radius: $border-radius-xs;
    padding: 0.125rem 0.25rem;
    // background-color: var(--mark-background-color);
    background-color: #fabea1;
    color: _var("body-color", $dark);
    vertical-align: baseline;
}
sub,
sup {
  font-size: 75%;
  line-height: 0;
  vertical-align: baseline;
}

sub {
  bottom: -.25rem;
}

sup {
  top: -.5rem;
}

address {
  padding: 0.5rem 0.75rem;
}

time {
  opacity: 0.80;
}

cite {
  font-size: 125%;
  font-weight: 500;
}

q {}

s {
  &:before,
  &:after {
    clip-path: inset(100%);
    clip: rect(1px, 1px, 1px, 1px);
    height: 1px;
    overflow: hidden;
    position: absolute;
    white-space: nowrap;
    width: 1px;

    &:before {
      content: " [start of stricken text] ";
    }

    &:after {
      content: " [end of stricken text] ";
    }
  }
}

data {
  opacity: 0.80;
}

