@charset "UTF-8";

//  Base -> Reset
// --------------------------------------------------
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100%;
  font-size: $font-size-pixel-base;
  // transition: 0.6s ease-in;

  // // 如果螢幕寬度為 576px 以下 就套用 css 設定
  @media (max-width: $screen-xs-min) {
    font-size: $font-size-pixel-xxs;

    @include font-scale($font-size-pixel-xxs);
  }

  // 如果螢幕寬度為 576px 以上 且 767.98px 以下，就套用 css 設定
  @media (min-width: $screen-xs-min) and (max-width: $screen-xs-max) {
    font-size: $font-size-pixel-xs;

    @include font-scale($font-size-pixel-xs);
  }

  // 如果螢幕寬度為 768px 以上 且 991.98px 以下，就套用 css 設定
  @media (min-width: $screen-sm-min) and (max-width: $screen-sm-max) {
    font-size: $font-size-pixel-sm;

    @include font-scale($font-size-pixel-sm);
  }

  // 如果螢幕寬度為 992px 以上 且 1199.98px 以下，就套用 css 設定
  @media (min-width: $screen-md-min) and (max-width: $screen-md-max) {
    font-size: $font-size-pixel-md;

    @include font-scale($font-size-pixel-md);
  }

  // 如果螢幕寬度為 1200px 以上 且 1399.98px 以下，就套用 css 設定
  @media (min-width: $screen-lg-min) and (max-width: $screen-lg-max) {
    font-size: $font-size-pixel-lg;

    @include font-scale($font-size-pixel-lg);
  }

  // 如果螢幕寬度為 1600px 以上 css 設定
  @media (min-width: $screen-xl-min) {
    font-size: $font-size-pixel-xxl;
    @include font-scale($font-size-pixel-xxl);
  }

}

body {
  background-color: _var("body-bg", $default);
  color: _var("body-color", $dark);
  font-family: $font-family-base;
  font-style: normal;
  line-height: $line-height-base;
  margin: 0;
  height: 100%;
  min-height: 100%;
  text-rendering: optimizeSpeed;
  overflow-x: hidden;
  // transition: 0.6s ease-in;
}

a,
button,
input,
textarea,
optgroup,
select,
[tabindex] {
  &:focus-visible {
    outline: 2px dashed rgba($primary, 0.72);
    outline-offset: 2px;
    border-radius: $border-radius-xs;
    transition: $transition-filter-link;
    box-shadow: 0 0 0 6px rgba($primary , 0.12);
  }
}