//
//  Components -> Masks 
// --------------------------------------------------

%mask-bg-cotain {
  background-size: cotain;
  background-repeat: repeat;
  background-position: top center;
}

%mask-bg-cover {
  background-size: cover;
  background-repeat: repeat;
  background-position: top center;
}

.mask {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  ~ * {
    position: relative;
  }

  &.mask_square {
    background: {
      image: url("data:image/svg+xml;base64,");
      size: 2rem 2rem;
      repeat: repeat;
      position-x: 0;
      position-y: 0;
    };

    @include opacity(0.72);
  }

