@charset "utf-8";

//  Symbol
// –––––––––––––––––––––––––––––––––––––––––––––––––––––––––
// Base
.t-symbol {
  display: inline-block;
  flex-shrink: 0;
  position: relative;

  @include border-radius($border-radius);

  // Label
  .t-symbol-label {
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 500;
    color: $gray-800;
    background-color: $gray-100;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;

    @include border-radius($border-radius);
  }

  // Badge
  .t-symbol-badge {
    position: absolute;
    border: 2px solid $body-bg;
    border-radius: 100%;
    top: 0;
    left: 50%;
    transform: translateX(-50%) translateY(-50%) !important;
  }

  // Image
  > img {
    width: 100%;
    flex-shrink: 0;
    display: inline-block;

    @include border-radius($border-radius);
  }

  // Square
  &.t-symbol-square {
    &,
    > img,
    .symbol-label {
      border-radius: 0 !important;
    }
  }

  // Circle
  &.t-symbol-circle {
    &,
    > img,
    .symbol-label {
      border-radius: 50%;
    }
  }

  // Sizes
  @include symbol-size(get($symbol-sizes, default));

  // @each $breakpoint in map-keys($grid-breakpoints) {
  //   @include media-breakpoint-up($breakpoint) {
  //     $infix: breakpoint-infix($breakpoint, $grid-breakpoints);

  //     @each $name, $value in $symbol-sizes {
  //       @if $name != "default" {
  //         &.symbol#{$infix}-#{$name} {
  //           @include symbol-size($value);
  //         }
  //       }
  //     }
  //   }
  // }
}

// Group
.t-symbol-group {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-left: 10px;

  .t-symbol {
    position: relative;
    z-index: 0;
    margin-left: -10px;
    border: 2px solid $body-bg;
    transition: all 0.3s ease;

    &:hover {
      transition: all 0.3s ease;
      z-index: 1;
    }
  }

  &.t-symbol-hover {
    .t-symbol {
      cursor: pointer;
    }
  }
}