//
//  Components -> Texts 
// --------------------------------------------------


.text-hide--2line {
  @include text_ellipsis(2, 1.5)
}

.text-hide--3line {
  @include text_ellipsis(3, 1.5)
}

.text-hide {
  @include text-truncate()
}

// Bg Grad Bottom
@each $color,
$value in $element-colors {
  @include text-bg-underline-variant("[data-grad-under=#{$color}]", $value);
}

// Bg before Border Bottom
@each $color,
$value in $element-colors {
  @include text-underline-before-variant("[data-bg-under=#{$color}]", $value);
}

// Bg Border Bottom
@each $color,
$value in $element-colors {
  @include text-underline-variant("[data-bd-under=#{$color}]", $value);
}

@each $color,
$value in $element-colors {
  @include text-underline-hv-lt2rt("[data-hv-under=#{$color}]", $value ,.0625rem);
}

@include text-underline-hv-variant(".underline-hv-secondary", $secondary);



