  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.toggle .knobs {
  z-index: 2;
}
.toggle .layer {
  -webkit-transition: 0.3s ease all;
  -o-transition: 0.3s ease all;
  transition: 0.3s ease all;
  width: 100%;
  z-index: 1;
}
.toggle .knobs:before {
  content: "開啟中";
  position: absolute;
  top: 0.125rem;
  left: 0.125rem;
  width: 3rem;
  height: 0.75rem;
  background-color: #1c84c6;
