/*  Filter Group
----------------------------------------  */
.filter-group {
  position: relative;
  background-color: #ffffff;
  border: solid #ffecd2 1px;
  border-radius: 3rem;
  display: inline-block;
  vertical-align: middle;
  overflow: hidden;
  z-index: 2;
}
.filter-group .chk-inp ~ .chk-lab {
  display: block;
  border-radius: 3rem;
  padding: 0.5rem 1.25rem;
}
.filter-group .chk-inp ~ .chk-lab .box {
  position: relative;
  border-radius: 4px;
  height: 1.375rem;
  width: 1.375rem;
}
.filter-group .chk-inp ~ .chk-lab .box:after {
  width: 0.5rem;
  height: 0.875rem;
}
.filter-group .chk-inp ~ .chk-lab:before {
  content: "";
  width: 10px;
  height: 10px;
  border-radius: 50%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) scale3d(1, 1, 1);
  transition: all 300ms cubic-bezier(0.4, 0, 0.2, 1);
  opacity: 0;
  z-index: -1;
}
.filter-group .chk-inp:checked ~ .chk-lab {
  color: #ffffff;
}
.filter-group .chk-inp:checked ~ .chk-lab:before {
  transform: translate(-50%, -50%) scale3d(56, 56, 1);
  opacity: 1;
}
.filter-group .chk-inp:checked ~ .chk-lab .box {
  background-color: #ffffff;
}
.filter-group.online:hover {
  background-color: white;
  color: #2b469b;
}
