  background-color: #4667CC;
}
.filter-group.online .chk-inp:checked ~ .chk-lab .box {
  border-color: #ffffff;
}
.filter-group.online .chk-inp:checked ~ .chk-lab .box:after {
  border: solid #4667CC;
  border-width: 0 0.1875rem 0.1875rem 0;
