  border: 2px solid #f8e87d;
}
.filter-group.onsite .chk-inp ~ .chk-lab .box:hover {
  border-color: #faf0ae;
}
.filter-group.onsite .chk-inp ~ .chk-lab .box:before {
  background-color: #F5DF4D;
}
