@charset "utf-8";

//  Components -> Headings 
// --------------------------------------------------

@each $color, $value in $theme-light-colors {
  .t-divider-heading-light-#{$color} {
    font-size: 1.75rem;

    @include text-divider-lt($background: $value, $height: 0.25rem, $width: 2.75rem);
  }
}

// <h1 class="t-heading-group">
//   <big class="t-heading">Taiwan Jobs 台灣就業通</big>
// </h1>
.t-section-heading {
  position: relative;
  display: flex;
  z-index: 1;

  &.t-red-heading {
    &.t-red-heading-right {
      &:before {
        right: 0;
        left: auto;
      }

      &:after {
        right: 7rem;
        left: auto;
      }
    }

    &:first-letter {
      font-size: 125%;
    }

    &:before {
      content: '';
      background-color: lighten($primary, 10%);
      border-radius: .5rem;
      width: 6.25rem;
      height: .5rem;
      position: absolute;
      left: 0;
      bottom: -.25rem;
      z-index: 1;
    }

    &:after {
      content: '';
      background-color: lighten($primary, 30%);
      border-radius: .5rem;
      width: 1.25rem;
      height: .5rem;
      position: absolute;
      left: 7rem;
      bottom: -.25rem;
    }
  }

