  .t-section-heading-main {
    position: relative;

    // display: inline-block;
    font-size: 2.25rem;
    letter-spacing: 1.5px;

    &::first-letter {
      font-size: 150%;
      color: $primary-600;
      margin-right: 0.05em;
    }

    &::before {
      content: '';
      position: absolute;
      left: 0;
      bottom: -2rem;
      width: 100%;
      height: 100%;
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      z-index: -1;
    }
  }

  .t-section-heading-lab {
    display: inline-flex;
    font-size: 1.75rem;
    font-weight: 700;
    color: $grey-200;
    margin-left: 1rem;
  }

  .t-section-heading-icon {
    font-size: 2rem;
    margin-right: 1rem;
    position: relative;
    display: inline-flex;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    width: 4rem;
    height: 4rem;
    vertical-align: bottom;
  }
}

// Graf 
// <h1 class="t-heading-group">
//   <big class="t-heading">Taiwan Jobs 台灣就業通</big>
// </h1>
.t-heading-group {
  position: relative;
  display: inline-flex;
  margin-bottom: 1.5rem;

  .t-heading {
    position: relative;
    font-weight: 500;
    font-size: 1.5rem;

    @include flex-center;

    margin-right: 3rem;
    letter-spacing: .03125rem;
    z-index: 1;

    &:before {
      content: '';
      background-image: linear-gradient(to top, $primary-100 15%, rgba($primary-50, 0) 40%);

      @include size($width: 100%);
      @include absolute($B: 0.125rem, $L: 0);

      transform: rotate(-0.5deg);
      z-index: -1;
    }
  }
}

// <h1 class="t-heading-icon-block">
// Taiwan Jobs 台灣就業通 
//  <ul class="t-relative t-block">
//   <li class="dot dot-1"></li>
//   <li class="dot dot-2"></li>
//   <li class="dot dot-3"></li>
//   <li class="dot dot-4"></li>
//   <li class="dot dot-5"></li>
// </ul>
// <div class="t-heading-icon">
//   <i class="svg-icon">
//     <svg>
//       <use href="#ic-gps"></use>
//     </svg>
//   </i>
// </div>
// </h1>
.t-heading-icon-block {
  display: flex;
  position: relative;

  .dot {
    @include border-radius(100%);
    @include absolute;
  }

  .dot-1 {
    background-color: #e4f3fd;

    @include size($width: 1.25rem);

    left: 0;
    top: 1rem;
  }

  .dot-2 {
    background-color: #ffeaee;

    @include size($width: 0.75rem);

    right: .5rem;
    top: 0;
  }

  .dot-3 {
    background-color: #e4f3fd;

    @include size($width: 0.625rem);

    right: .5rem;
    bottom: .5rem;
  }

  .dot-4 {
    background-color: #ffeaee;

    @include size($width: 1.5rem);

    left: -.125rem;
    top: 0;
  }

  .t-heading-icon {
    font-size: 2.25rem;
    color: $lightgrey;
    z-index: 1;
  }
}