.t-grid-mask {
  background-position: 10px 10px;
  mask-image: linear-gradient(0deg, rgba($white, 0.1), rgba($white, 0.5));
  background-image: t-svg(url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 32 32' width='32' height='32' fill='none' stroke='#{$dark}'%3e%3cpath d='M0 .5H31.5V32'/%3e%3c/svg%3e"));
}

.t-bg-mask-grid {
  background-position: 10px 10px;
  mask-image: linear-gradient(0deg, rgba($white, 0.1), rgba($white, 0.5));
  background-image: t-svg(url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 32 32' width='32' height='32' fill='none' stroke='#{$dark}'%3e%3cpath d='M0 .5H31.5V32'/%3e%3c/svg%3e"));
}

.t-bg-mesh-primary {
  @include gradient-top-mesh($gray-50 , $primary-50, 0.66);
}

.t-bg-slash-primary {
  @include gradient-slash(45deg, $primary-600, $primary-400);
}

.t-bg-half-primary {
  @include gradient-half(at center bottom, $primary-600, $primary-400);
}



