.invalid-tooltip {
  line-height: 1.45;
  max-width: 100%;

  @include border-radius(0.25rem);
  @include absolute($T: 0, $R: 0);

  font-size: $font-size-xxs;
  margin-top: .1rem;
  padding: .25rem .5rem;
  color: $white;
  background-color: $danger;
}

.valid-tooltip {
  line-height: 1.45;
  max-width: 100%;

  @include border-radius(0.25rem);
  @include absolute($T: 0, $R: 0);

  font-size: $font-size-xxs;
  margin-top: .1rem;
  padding: .25rem .5rem;
  color: $white;
  background-color: $success;
}

.field-validation-error {
  @extend .invalid-tooltip;
}

.field-validation-success {
  @extend .valid-tooltip;
}

.input-validation {
  &-error {
    border-color: $danger;
  }

  &-success {
    border-color: $success;
  }
}

/*  INPUT ICON
----------------------------------------  */
.input-icon-rt {
  cursor: pointer;
  color: lightgrey;
  font-size: 1rem;

  @include absolute($R: 1rem, $T: 50%);

  transform: translateY(-50%);

  &:hover {
    color: $primary;
  }
}

.input-group {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
  width: 100%;

  > i:first-of-type {
    color: $lightgrey;
    font-size: 1.25rem;

    @include absolute($T: 50%, $L: 1rem);

    transform: translateY(-50%);
    transition: $transition-base;
  }

  .t-form-control {
    padding-left: 4rem;

    &:focus {
      + i {
        color: $primary;
      }
    }
  }
}

.input-group-txt {
  .input-txt {
    font-size: 1rem;

    @include absolute($T: 50%, $R: 1rem);

    transform: translateY(-50%);
  }

  .t-form-control {
    padding-right: 3rem;
  }
}